import React from 'react';
import { useEffect } from 'react';
import Project from './Project';
import {Container} from 'react-bootstrap';


var pJects;


const Projects = (props) => {
    
    function renderProjects(){

        if(pJects != null)
        {
        pJects = pJects.map(x => {
            return (
                <div>
                    <Project key={x.name} name={x.name} creator={x.creator} />
                </div>
            )
        });
        return pJects;
        } else {
            return <div>Currently Under Construction</div>
        }
    };

    useEffect(() => {
        document.title = "Brandon Hogan | Projects";
    });

    return (
        <div className="main">
            <div className="header">
                <Container>
                    {/* This is where the header profile image will go */}
                    <h1 className="h-title">Projects</h1>
                    <h2 className="h-lead">A compiled list of some of my work and side projects. Enjoy!</h2>
                </Container>
            </div>
            <Container className="sub-cat">
                {renderProjects()}
            </Container>
        </div>
    )
}

export default Projects;